import React from 'react'
import Link from 'gatsby-link'
import art2 from '../images/Willaa-Space-Diva-Art.jpg';
import art1 from '../images/willaa-how-u-sey-art.jpg';
import art3 from '../images/Willaa-EYEDU-art.jpg';
import willaa from '../images/willaa-2.png';
import BackNav from '../components/BackNav'

const MusicPage = () =>
  <div>
    <div className="relative main padding-bottom-50">
      <BackNav />
      <h1>🎶 Music Please</h1>
      <div className="indent">
        <p>I DJ under the name Willaa, writing and playing house music.</p>
        <p className='margin-top-50'>Latest mix of all-original tracks:</p>
        <iframe width="100%" height="166" scrolling="no" frameborder="no" src="https://w.soundcloud.com/player/?url=https%3A//api.soundcloud.com/tracks/346922022&amp;color=%23ff5500&amp;auto_play=false&amp;hide_related=false&amp;show_comments=true&amp;show_user=true&amp;show_reposts=false&amp;show_teaser=true"></iframe>
        <p className='margin-top-50'>Self-released music:</p>
        <div className="flex-row">
          <div className="item">
            <a target="_blank" href="https://soundcloud.com/djwillaa/sets/how-u-sey-ep">
              <img className="img-full art" src={art1} />
            </a>
            <p className='margin-none'>How U Sey EP</p>
            <span>
              <a target="_blank" href="https://soundcloud.com/djwillaa/sets/how-u-sey-ep">Stream</a> | <a target="_blank" href="https://www.dropbox.com/sh/gixolmpmut07f8a/AAD4X8einXv2LwDJzA82_qBEa?dl=0">Download</a>
            </span>
          </div>
          <div className="item">
            <a target="_blank" href="https://soundcloud.com/djwillaa/sets/space-diva-ep-preview">
              <img className="img-full art" src={art2} />
            </a>
            <p className='margin-none'>Space Diva EP</p>
            <span>
              <a target="_blank" href="https://soundcloud.com/djwillaa/sets/space-diva-ep-preview">Stream</a> | <a target="_blank" href="https://www.dropbox.com/sh/24xhb7wwd7i2pp0/AABr7h8ylwtaJFUAwFNjljFfa?dl=0">Download</a>
            </span>
          </div>
          <div className="item">
            <a target="_blank" href="https://soundcloud.com/djwillaa/eyedu-original-mix">
              <img className="img-full art" src={art3} />
            </a>
            <p className='margin-none'>EYEDU [Single]</p>
            <span>
              <a target="_blank" href="https://soundcloud.com/djwillaa/eyedu-original-mix">Stream</a> | <a target="_blank" href="https://www.dropbox.com/s/io5brgfjq3ozbh5/Willaa%20-%20EYEDU%20%28Original%20Mix%29%20.wav?dl=0">Download</a>
            </span>
          </div>
        </div>
      </div>
      <p className="indent margin-top-50">To stay up to date: <a href="mixcloud.com/willaa/">follow my mix series on Mixcloud</a>, and <a href="facebook.com/djwillaa/">like my DJ Facebook Page.</a></p>
      <Link className='indent' to='/'>Back to Home</Link>
    </div>
  </div>

export default MusicPage



